import uniCountdown from '@/components/uni-countdown/uni-countdown.vue';
import moveVerify from '@/components/helang-moveVerify/helang-moveVerify.vue';
export default {
	data() {
		return {
			username: '',
			phone: '',
			verify: '',
			//验证的规则
			rules:{
				username:{
					rule:/\S/,
					msg:"姓名不能为空"
				},
				phone:{
					rule:/^1[3|4|5|6|7|8][0-9]\d{8,8}$/,
					msg:"手机号码不正确"
				},
				verify:{
					rule:/^[A-Za-z0-9]{4}$/,
					msg:"验证码不正确"
				},
			},
			resultData: {},
			isVerify: false,
			isSend: true,
			sendText: '发送验证码'
		};
	},
	components: { 'move-verify': moveVerify, uniCountdown },
	methods: {
		/* 校验结果回调函数 */
		verifyResult(res) {
			if (res.flag) {
				this.isVerify = true;
			}
			this.resultData = res;
		},
		/* 校验插件重置 */
		verifyReset() {
			this.$refs.verifyElement.reset();
			/* 删除当前页面的数据 */
			this.resultData = {};
		},
		isOver() {
			this.sendText = '再次发送';
			this.isSend = true;
		},
		sendVerify() {
			this.isSend = false;
		},
		//
		submit(){
			if(!this.validate('username')) return;
			if(!this.validate("phone"))  return;
			if(!this.validate("verify"))  return;
			uni.showLoading({
				title:"提交中"
			});
			setTimeout(()=>{
				//隐藏登录状态
				uni.hideLoading();
				console.log( "111 ")
			},2000)
		},
		//判断验证是否符合要求
		validate(key){
			let bool=true;
			if(!this.rules[key].rule.test(this[key])){
				//提示信息
				uni.showToast({
					title:this.rules[key].msg,
					icon: 'none' 
				})
				//取反
				bool=false;
				return false;
			}
			return bool;
		}
	},
};
